.header{
    background-color:var(--background-color);
    height:auto;
    display:flex;
    flex-flow:column wrap;
    padding:50px;
    align-items:flex-start;
  }

.headerOvalos{
    width:100%;
    max-width:350px;
    display:block;
    margin:0 auto;
}
  
  .header-text{
    display:flex;
    flex-flow:column wrap;
    margin:40px auto;
  }
  
  .header-text h2{
    font-size:1.5rem;
    margin-top:5px;
    letter-spacing: 0.5px;
  }
  
  .header-text h2:first-child{
    font-style:italic;
  }
  
  .header-text h1 span{
    color:var(--orange-color);
  }
  
  .header-text h1{
    margin-top:10px;
    font-size:1.8rem;
    margin-bottom:7px;
    
  }
  
  .header-text p{
    margin-top:30px;
    line-height:30px;
    font-family: "Lato", serif;
    font-weight: 400;
    font-size:1.15rem;
    opacity:0.9;
    letter-spacing: 0.5px;
  }

  button{
    width:160px;
    height:40px;
    font-size:1.1rem;
    font-family: "Playfair Display", serif;
    padding:5px;
    line-height:15px;
    border:none;
    border-radius:5px;
    box-shadow:2px 2px 4px rgb(0,0,0,0.25);
    margin-top:20px;
    font-weight:600;
    cursor:pointer;
    color:var(--text-color);
  }

  .btn-viewProjects{
    margin-top:20px;
    background-color:var(--orange-color);
  }

  .btn-viewProjects:hover{
    background-color: var(--text-color);
    color:var(--background-color);
    transform:scale(1.05);
  }

  .btn-hireMe{
 
    background-color: var(--blue-color);
  }

  .btn-hireMe:hover{
    background-color: var(--text-color);
    color:var(--orange-color);
    transform:scale(1.05);  
  }


